body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


nav {
  padding: 0 1rem;
}

.content {
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.goods {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content {
  flex-grow: 1;
}

.cart {
position: fixed;
bottom: 2rem;
right: 2rem;
z-index: 5;
cursor: pointer;


}

@media (min-width: 767px){
  .cart {
    top: 5rem;
    bottom: unset;
  }
}



.basketList {

    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 5;

    width: 50rem;
    max-width: 95%;

    animation: show 300ms ease-out;
}

i {
  cursor: pointer;
}

.change-amount-btn {

width: 20px;
height: 20px;
margin: 0 10px;

}
.change-amount-btn i {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate( -50%, -50%);

}

@keyframes show {
  from {
    top: 30%;
    left: 70%;
    opacity: 0;

  }
  to {
    top: 50%;
    left: 50%;
    opacity: 1;
  }
}

#toast-container {
  top: 2rem;
  left: 70%;
  animation: fade 300ms ease-in-out;
}

@keyframes fade {
  from{opacity: 0;
  transform: translateY(50%);}
  to{opacity: 1;
  transform: translateY(0);}
}
